<template>
  <div class="zlogs">
    <div class="top-wrap">
      <div class="label" @click="$router.push('/')">z7xisoo</div>
      <div class="links">
        <a class="item" target="_blank" href="https://github.com/z7xisoo">Github</a>
        <div class="item" @click="$router.push('/zi')">About</div>
      </div>
    </div>
    <div class="content-wrap">
      <div :class="['iphone-wrap', markdown && 'white']">
        <div
          :key="timeNumber"
          class="phone-time"
        >{{new Date().getHours()}}:{{new Date().getMinutes() > 9 ? new Date().getMinutes() : `0${new Date().getMinutes()}`}}:{{new Date().getSeconds() > 9 ? new Date().getSeconds() : `0${new Date().getSeconds()}`}}</div>

        <template v-if="showBlog">
          <div class="list">
            <div
              v-for="(item,key) in mds"
              :key="key"
              @click="handleClick(item,key)"
              :class="[isId === key && 'select','item']"
            >
              <div class="tags">
                <div class="tag-item" v-for="item in item.tag" :key="item">{{item}}</div>
              </div>
              <h1 class="label" :title="item.label">{{item.label}}</h1>
              <div class="time">
                <div>{{item.time}}</div>
                <div class="do" v-for="item in '123456'" :key="item"></div>
              </div>
            </div>
          </div>
        </template>
        <template v-else>
          <div class="home" @click="handleToblogs">z7xisoo</div>
        </template>

        <div class="phone-back" @click="back"></div>
      </div>
      <div v-if="markdown" class="markdown-wrap">
        <h1 class="markdown-title">{{title}}</h1>
        <MarkdownPreview v-if="markdown" :initialValue="markdown" theme="oneDark" />
      </div>
      <div v-else class="markdown-wrap no-markdown">
        <RainbowWaves :config="config" :waves="waves" />
        <div class="text">
          至
          <br />自律,
          <br />自控,
          <br />自持,
          <br />自省,
          <br />自视,
          <br />自重,
          <br />自修,
          <br />来日方可期
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import mds from "./mds.json";
import { MarkdownPreview } from "vue-meditor";
import RainbowWaves from "rainbow-waves";

export default {
  name: "Zlogs",
  components: {
    MarkdownPreview,
    RainbowWaves,
  },
  computed: {},
  watch: {},
  data() {
    return {
      mds,
      markdown: null,
      config: {},
      isId: "1",
      showBlog: true,
      timeNumber: 0,
      timeInterval: null,
      title: "",
      config: {
        background: {
          type: "color",
          color: "orange",
        },
        direction: "right",
      },
      waves: [
        {
          background: {
            type: "color",
            color: "rgba(255,255,0,0.5)",
            position: [0, 0, 0, 1080],
          },
          jitter: 0.04,
          restore: 0.03,
          waveGap: 80,
          waterGap: 20,
          waveUps: 6,
          bit: 0.3,
        },
      ],
    };
  },
  watch: {},
  mounted() {
    this.handleFirstMD();
    this.timeInterval = setInterval(() => {
      this.timeNumber += 1;
    }, 1000);
  },
  beforeDestroy() {
    if (this.timeInterval) {
      clearInterval(this.timeInterval);
    }
  },
  methods: {
    handleToblogs() {
      this.showBlog = true;
      this.handleFirstMD();
    },
    handleFirstMD() {
      this.title = this.mds["1"]["label"];
      this.markdown = require(`@/zlogs/${this.mds["1"]["url"]}`);
    },
    handleClick(item, key) {
      this.isId = key;
      this.title = item.label;
      this.markdown = require(`@/zlogs/${item.url}`);
    },
    back() {
      this.showBlog = !this.showBlog;
      if (this.showBlog) {
        this.handleFirstMD();
      } else {
        this.markdown = null;
      }
    },
  },
};
</script>

